<template>
  <div class="login">

    <!-- Block of alert notifications-->
    <div class="allert__mes" v-if="errorLogData == 'true'">
      <b-message type="is-danger" has-icon auto-close>  
        Неправильный логин или пароль. Пожалуйста, попробуйте еще раз.
      </b-message>
    </div>

    <div class="allert__mes" v-if="manyLogins == 'true'">
      <b-message type="is-warning" has-icon auto-close>  
        Cлишком много попыток. Попробуйте, пожалуйста, позже.
      </b-message>
    </div>

    <div class="allert__mes" v-if="badConnection == 'true'">
      <b-message type="is-warning" has-icon auto-close>  
        Похоже, у вас проблемы с интрнетом. Проверьте ваше соеденение.
      </b-message>
    </div>


    <div class="form__container">
      <div class="preloader">
        <b-notification ref="element" :closable="false" class="preloader"></b-notification>
      </div>  

      <h1 class="form__title">Вход в систему</h1>

      <form id="loginform" @submit.prevent="checkValidation" class="form__login" novalidate="true">
        <div class="form__alert-validation" v-if="errors.length > 0">
          <p>Корректно заполните поле(я):</p>
          <ul>
            <li class="form__alert-list" v-for="(error,index) in errors" :key="index">{{ error }}</li>
          </ul>
        </div>

        <b-field>
          <b-input
            placeholder="Введите ваш логин"
            type="email"
            validation-message="Некоректно введена почта"
            required
            v-model="login"
            id="login"
            custom-class="form__login-field"
            expanded>
          </b-input>
        </b-field>

        <b-field>
            <b-input 
              type="password"
              custom-class="form__login-field"
              required
              id="password"
              v-model="password"
              placeholder="Введите ваш пароль"
              validation-message="Поле пароль не может быть пустым"
              password-reveal
              expanded>
            </b-input>
        </b-field>

        <input class="form__login-field form__login-submit" type="submit" value="Войти"/>
      </form>

    </div>
  </div>
</template>

<script>
import * as firebase from "firebase/app";
import "firebase/auth";

export default {
  data: function(){
    return{
      password: null,
      login: null,
      errors: [],
      firebaseConfig: '',
      errorLogData: false,
      manyLogins: false,
      badConnection: false,
    }
  },
  methods: {
    authorization(){
    
    const loader = this.$buefy.loading.open({container: this.$refs.element.$el});

    firebase.auth().setPersistence(firebase.auth.Auth.Persistence.SESSION).then( _=>{    
      firebase.auth().signInWithEmailAndPassword(this.login, this.password).then( res => {
        let currentUser = firebase.auth().currentUser.providerData[0].email;
        this.$store.dispatch('setLoginUser', currentUser);
      })
      .then(_=>{
        const link = this.$route.query.redirect || '/dashboard';
        loader.close();
        this.$router.push(link); 
      })
      .catch(error => {
        console.log('Error:'+error.message);

        if(error.message == 'There is no user record corresponding to this identifier. The user may have been deleted.' ||
           error.message == 'The password is invalid or the user does not have a password.'){   
            this.$router.go();
            localStorage.errorLogData = true; 
        }else if(error.message =='Too many unsuccessful login attempts. Please try again later.'){
            this.$router.go();
            localStorage.manyLogins = true;
            } else if(error.message =='A network error (such as timeout, interrupted connection or unreachable host) has occurred.'){
            this.$router.go();
            localStorage.badConnection = true;
          }
        }); 
      })     
    },
    checkValidation: function(e){
      if(this.login && this.password && this.validEmail(this.login)) {this.authorization()}
      else{
        this.errors = [];

        if(!this.login) {this.errors.push("Поле логина")}
        else if(!this.validEmail(this.login)){this.errors.push("Поле логина")}

        !this.password ? this.errors.push("Поле пароля") : null;
      }
    },
    validEmail: function (email) {
      let reg = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return reg.test(email);
    }
  },
  created(){
      if (!firebase.apps.length) {
      this.firebaseConfig = this.$store.getters.getfirebaseConfig;
      firebase.initializeApp(this.firebaseConfig);

      this.$store.dispatch('setLoginUser', null);
    }
  },
  mounted() {
    if (localStorage.errorLogData) {
      this.errorLogData = localStorage.errorLogData;
    }
    if (localStorage.manyLogins) {
      this.manyLogins = localStorage.manyLogins;
    }
    if (localStorage.manyLogins) {
      this.badConnection = localStorage.badConnection;
    }
    localStorage.badConnection = false;
    localStorage.errorLogData = false;
    localStorage.manyLogins = false;
  },
}
</script>

